@import url(https://fonts.googleapis.com/css2?family=Gluten:wght@700&display=swap);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 64.5%;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.6rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.heading {
  font-family: "Gluten", sans-serif;
  letter-spacing: 2px;
  line-height: 75px;
  text-align: center;
  background-color: blue;
  color: white;
  font-size: 3.6rem;
}

.score {
  background: blue;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  border-bottom: 14px solid black;
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
  padding-bottom: 10px;
}
.score h2 {
  padding: 14px;
  color: white;
  letter-spacing: 3px;
}
.score span {
  font-size: 2rem;
  padding-left: 10px;
  color: yellow;
  font-weight: bold;
  font-family: cursive;
}
.vr {
  width: 2px;
  height: 35px;
  background-color: white;
  border-radius: 10px;
}

.sub_heading {
  text-align: center;
  padding: 10px 0;
}
.cards {
  margin-bottom: 30px;
  padding: 0 20px;
}
.cards__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.card {
  height: 200px;
  width: 160px;
  background-color: rgba(0, 0, 255, 0.6);
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly;
  border-radius: 4px;
  border: 4px solid black;
  margin: 10px;
  cursor: pointer;
  transition: all 0.3s;
}
.card:hover {
  background-color: rgba(0, 0, 255, 0.55);
}
.card img {
  object-fit: contain;
  pointer-events: none;
}
.name {
  padding: 10px 0;
  font-size: 2rem;
  color: white;
  font-weight: bold;
  text-transform: capitalize;
  pointer-events: none;
}
.message {
  text-align: center;
  padding: 30px 0;
  font-size: 2rem;
}
.message button {
  padding: 10px 15px;
  background-color: blue;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
  border-radius: 4px;
  margin-top: 10px;
}
.message button:hover {
  letter-spacing: 1px;
}

